const GRAPHQL_API_URL = "https://hypha.us-east-1.aws.cloud.dgraph.io/graphql";
const IPFS_GATEWAY = "https://hypha.infura-ipfs.io/ipfs/";
const hyphaAccessTokenKey = "hyphaAccessToken";
const hyphaRefreshTokenKey = "hyphaRefreshToken";
import { login, logout, restoreSession, session } from "./wallet";

function ipfsy(cid) {
  if (!cid) {
    // console.warn('Invalid cid') // eslint-disable-line no-console
    return undefined;
  }
  return IPFS_GATEWAY + cid.replace(/:.*$/, "");
}

function calculatePercentage(part, total) {
  return (part / total) * 100;
}

// process.env.CHAIN_NAME ||
// process.env.ENV ||
// process.env.HYPHA_AUTH_URL ||
//
const _fetchAccessToken = async () => {
  const HYPHA_AUTH_URL =
    "https://y3b2ihsdv7.execute-api.us-east-1.amazonaws.com";
  const CHAIN_NAME = "telos";
  const ENV = "prod";
  const network = "mainnet";
  //process.env.IS_TESTNET ? 'testnet' : 'mainnet'
  const url = `${HYPHA_AUTH_URL}?chain=${CHAIN_NAME}&env=${ENV}&network=${network}`;

  try {
    const request = await fetch(url);
    const data = await request.json();

    localStorage.setItem(hyphaAccessTokenKey, data.accessJWT);
    localStorage.setItem(hyphaRefreshTokenKey, data.refreshJWT);
  } catch (error) {}
};

interface QueryOptions {
  operationsDoc: String;
  variables: Object;
  operationName: String;
}

const query = async (QueryOptions) => {
  const request = await fetch(GRAPHQL_API_URL, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      // "X-Auth-Token": "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhdWQiOiJzL3Byb3h5IiwiZHVpZCI6IjB4ODI5ZjkxMTlmIiwiZXhwIjoxNzE0NTgyMjMxLCJpc3MiOiJzL2FwaSJ9.AFkLOcIDjqOM-_0fyFS3n7zj2_mtys9J1o1WrKhNdwE",
      "X-Dgraph-AccessToken": localStorage.getItem(hyphaAccessTokenKey) || "",
    },
    body: JSON.stringify({
      query: QueryOptions.operationsDoc,
      variables: QueryOptions.variables,
      operationName: QueryOptions.operationName,
    }),
  });

  const { data, errors } = await request.json();

  if (errors && errors.length > 1) {
    _fetchAccessToken();
    query({
      query: QueryOptions.operationsDoc,
      variables: QueryOptions.variables,
      operationName: QueryOptions.operationName,
    });
  }

  return data;
};

export async function getStats() {
  const operationsDoc = `
    query stats {
      aggregateDao {
        count
      }
      aggregateMember {
        count
      }
      aggregateVotable {
        count
      }
      }
    `;

  const variables = {};

  const operationName = "stats";

  const data = await query({
    operationsDoc: operationsDoc,
    variables: variables,
    operationName: operationName,
  });

  return {
    totalDHOCount: data?.aggregateDao?.count,
    totalPeopleCount: data?.aggregateMember?.count,
    totalAgreementsCount: data?.aggregateVotable?.count,
  };
}

export async function getDHO(dhoName: String) {
  const operationsDoc = `
    query dho($regexp: String!) {
      queryDao @cascade(fields: ["settings"]) {
        docId
        details_daoName_n
        details_daoType_s
        createdDate

        settings(filter: {settings_daoUrl_s: {regexp: $regexp}}) {
          settings_onboarderAccount_n
          settings_claimEnabled_i
          settings_daoUrl_s
          settings_daoName_n
          settings_daoTitle_s
          settings_daoDescription_s
          settings_governanceTokenContract_n
          settings_pegTokenName_s
          settings_pegToken_a
          settings_pegTokenContract_n
          settings_treasuryTokenMultiplier_i
          settings_treasuryContract_n
          settings_rewardTokenName_s
          settings_rewardToken_a
          settings_rewardTokenContract_n
          settings_rewardToPegRatio_a
          settings_rewardTokenMaxSupply_a
          settings_utilityTokenMultiplier_i
          settings_voiceToken_a
          settings_voiceTokenDecayPeriod_i
          settings_voiceTokenDecayPerPeriodX10M_i
          settings_voiceTokenMultiplier_i
          settings_socialChat_s
          settings_documentationButtonText_s
          settings_proposalsCreationEnabled_i
          settings_membersApplicationEnabled_i
          settings_removableBannersEnabled_i
          settings_multisigEnabled_i
          settings_votingDurationSec_i
          settings_periodDurationSec_i
          settings_votingAlignmentX100_i
          settings_votingQuorumX100_i
          settings_voiceTokenDecayPeriod_i
          settings_communityVotingEnabled_i
          settings_communityVotingMethod_s
          settings_upvoteStartDateTime_s
          settings_upvoteDuration_i
          settings_upvoteRounds_s
          settings_upvoteCheifDelegateCount_i
          settings_upvoteCheifDelegateDuration_i
          settings_upvoteHeadDelegateRound_i
          settings_upvoteHeadDelegateDuration_i
          settings_communityVotingDurationSec_i
          settings_communityVotingAlignmentPercent_i
          settings_communityVotingQuorumPercent_i
          settings_usesSeeds_i
          settings_isHypha_i
          settings_logo_s
          settings_extendedLogo_s
          settings_primaryColor_s
          settings_secondaryColor_s
          settings_textColor_s
          settings_pattern_s
          settings_patternColor_s
          settings_patternOpacity_i
          settings_patternBase64_s
          settings_splashBackgroundImage_s
          settings_dashboardBackgroundImage_s
          settings_dashboardTitle_s
          settings_dashboardParagraph_s
          settings_proposalsBackgroundImage_s
          settings_proposalsTitle_s
          settings_proposalsParagraph_s
          settings_membersBackgroundImage_s
          settings_membersTitle_s
          settings_membersParagraph_s
          settings_organisationBackgroundImage_s
          settings_organisationTitle_s
          settings_organisationParagraph_s
          settings_exploreBackgroundImage_s
          settings_exploreTitle_s
          settings_exploreParagraph_s
          settings_documentationUrl_s
        }

        memberAggregate {
          count
        }

        proposalAggregate {
          count
        }
      }
    }
    `;

  const variables = {
    regexp: `/^${dhoName}$/i`,
  };

  const operationName = "dho";

  //This can be plugin that is able to query diffrent db?
  //it needs to have shared interface

  const data = await query({
    operationsDoc: operationsDoc,
    variables: variables,
    operationName: operationName,
  });

  const dao = { ...data?.queryDao[0], ...data?.queryDao[0]?.settings[0] };

  //TODO: Query how much stake the dao has
  const stakeAmount = 0;
  const stakeRequired = dao.memberAggregate?.count * 5;
  const stakeMissing = Math.abs(stakeAmount - stakeRequired);

  return {
    name: dao.settings_daoTitle_s,
    logo: ipfsy(dao.settings_logo_s),
    purpose: dao.settings_daoDescription_s,

    agreementCount: dao.proposalAggregate?.count,
    peopleCount: dao.memberAggregate?.count,
    tokenCount: 0,

    activeSince: dao.createdDate,
    href: dao.settings_daoUrl_s,

    stakeAmount: stakeAmount,
    stakeRequired: stakeRequired,
    stakeMissing: stakeMissing,

    stakeProgress: Math.floor(calculatePercentage(stakeAmount, stakeRequired)),
  };
}

export async function listDHO(opts) {
  const operationsDoc = `
      query daoList($order: DaoOrder, $filter: DaoFilter, $first: Int!, $offset: Int!) {
        queryDao(order: $order, filter: $filter, first: $first, offset: $offset) {
          docId
          details_daoName_n
          createdDate
          settings {
            settings_daoDescription_s
            settings_daoTitle_s
            settings_daoName_n
            settings_logo_s
            settings_primaryColor_s
            settings_secondaryColor_s
            settings_daoUrl_s
          }
          memberAggregate {
            count
          }
          proposalAggregate {
            count
          }
        }
      }
    `;

  const variables = opts || {
    order: { asc: "createdDate" },
    filter: {},
    first: 10,
    offset: 0,
  };

  const operationName = "daoList";

  const data = await query({
    operationsDoc: operationsDoc,
    variables: variables,
    operationName: operationName,
  });

  return {
    dhoList: data?.queryDao?.map((dao) => {
      //TODO: Query how much stake the dao has
      const stakeAmount = 0;
      const stakeRequired = dao?.memberAggregate?.count * 5;
      const stakeMissing = Math.abs(stakeAmount - stakeRequired);

      return {
        name: dao?.settings[0]?.settings_daoTitle_s,
        logo: ipfsy(dao?.settings[0]?.settings_logo_s),
        href: dao?.settings[0]?.settings_daoUrl_s,
        activeSince: dao?.createdDate,
        agreementCount: dao?.proposalAggregate?.count,
        peopleCount: dao?.memberAggregate?.count,
        stakeAmount: stakeAmount,
        stakeRequired: stakeRequired,
        stakeMissing: stakeMissing,
        stakeProgress: Math.floor(
          calculatePercentage(stakeAmount, stakeRequired),
        ),
      };
    }),
  };
}

//mutations

/**
 * Convert from given Uint64 to a EOS's name format.
 * @param {String} value A string represents uint64
 * @returns
 */

interface StakeOptions {
  to: String;
  quantity: string;
}

async function stake(opts: StakeOptions) {
  const action = {
    account: "stake.hypha",
    name: "stake",
    authorization: [session.permissionLevel],
    data: {
      from: session.actor,
      to: opts.to,
      quantity: `${parseFloat(opts.quantity).toFixed(2)} HYPHA`,
    },
  };
  const result = await session.transact({ action });

  return result;
}

(window as any).HyphaHDK = {
  //queries
  getDHO,
  getStats,
  listDHO,

  //mutations
  stake,

  //utils
  wallet: { login, logout, restoreSession },
};
